import list from './views/list'
import upload from './views/upload'

let title = ' - 又拍云CDN'

export default {
  routes: [
    {path: '', name: 'upyun-list', component: list, meta: {title: '列表' + title}},
    {path: 'upload', name: 'upyun-upload', component: upload, meta: {title: '上传' + title}}
  ]
}



